.canvas_text_container_home {
    position: absolute;
    margin-top: calc(-100vh + 50px);
    padding-left: 100px;
    padding-top: 100px;
    z-index: 2;
}

.canvas_text_container_home_name {
    margin-bottom: 20px;
}

.canvas_text_container_home_name h1 {
    font-family: 'Montserrat', sans-serif;
    margin: 0%;

}

.canvas_text_container_home_text {
    font-size: 1.25rem;
    max-width: 450px;
}

.canvas_text_container_home_text div {
    margin-top: 20px;
}

.canvas_text_container_home_text div span,
span {
    color: #f08080;
}

.canvas_text_container_home_text_typeit {
    font-family: 'Mulish', sans-serif;
    font-size: 1.25rem;
    color: #f08080;
}

.canvas_text_container_glassmorphism {
    position: relative;
    min-width: 500px;
    min-height: 400px;
    background: rgba(255, 255, 255, .2);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    margin-top: -70%;
    margin-left: -10%;
    border-radius: 50px;
    /* filter: blur(5px); */
    z-index: -1;
}

.canvas_icon_container {
    margin: 0%;
    margin-top: 50px;


}

.canvas_icon_container_Icon {

    display: inline-flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: flex-start;
    overflow: hidden;
    background: rgba(255, 255, 255, 0.25);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    height: 50px;
    width: 50px;
    transition: 300ms ease-in-out;
    margin-right: 20px;
    text-decoration: none;

}

.canvas_icon_container_Icon:hover {


    width: 150px;

}

.canvas_icon_container_Icon_content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    width: 50px;
    height: 50px;
    padding: 0%;
    margin: 0%;

    font-size: 1.5rem;
    transition: 300ms ease-in-out;
    ;
}

.canvas_icon_container_Icon_content:hover {
    margin-right: 10px;
}

.canvas_icon_container_Icon a {
    text-decoration: none;
    color: black;
}