.about_text_container_outer {
    position: absolute;
    margin-top: calc(-100vh + 50px);
    /* padding-left: 100px;
    padding-top: 100px; */
}

.about_text_container_inner {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;
    width: 550px;
    min-height: auto;
    background: rgba(255, 255, 255, .2);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    margin-top: 50px;
    margin-left: 60px;
    border-radius: 50px;
    padding-bottom: 20px;
    margin-bottom: 50px;
}

.about_text_container_Education_Detail_container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about_text_container_title {

    font-family: 'Montserrat', sans-serif;

}

.about_text_container_discription {
    padding-left: 50px;
    padding-right: 50px;
    font-size: 1.12rem;
}

.about_text_container_Education_Detail_tilte {

    font-family: 'Montserrat', sans-serif;
    font-size: 1.2rem;
    font-weight: bold;
}

.about_text_container_Education_Detail {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 1.12rem;
}