html,
body,
#root,
canvas {
  margin: 0;
  padding: 0%;
  width: 100%;
  height: 100%;
}

canvas {
  position: fixed;
  z-index: -1;

}

body {
  background-image: linear-gradient(to right top, #fbeafe, #eeecff, #dcf0ff, #caf4ff, #bef7ff);

  background-attachment: fixed;
  position: absolute;
}

.Navbar {
  position: fixed;
  width: 100%;
  z-index: 3;

}

.Navbar_Header {
  position: absolute;

  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0%;
  margin: 0%;
  color: #f08080;
  background-color: #ffb5a7;

}

.Navbar_Header h1 {
  position: relative;
  margin: 0%;
  padding: 0%;
  padding-left: 1.25rem;
  margin-bottom: .25rem;
  cursor: pointer;

}

.Navbar_Menu {
  display: flex;
  padding: 0%;
  padding-right: 1rem;

}

.Navbar_Menu h3 {
  font-size: 1.3rem;
  font-weight: 500;
  margin-top: 0%;
  margin-bottom: 0%;
  padding: 0 1rem;
  margin-top: 0rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  cursor: pointer;
}