.skills_text_container_outer {
    position: absolute;
    margin-top: calc(-100vh + 50px);
}

.skills_text_container_inner {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;
    width: 550px;
    min-height: auto;
    background: rgba(255, 255, 255, .2);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    margin-top: 50px;
    margin-left: 60px;
    border-radius: 50px;
    margin-bottom: 80px;
    padding-bottom: 50px;
    padding-top: 15px;

}

.skills_text_container_title {
    padding-top: 20px;
    font-size: 1.3rem;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
}

.skills_text_content {
    position: relative;
    width: 450px;
    padding-left: 0%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

}

.skills_content_progress_bar {

    width: 100%;
    height: 12px;
    border-radius: 6px;
    background-color: aliceblue;
}

.skills_content_progress_fil {

    width: 50%;
    height: 12px;
    border-radius: 6px;
    background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
    ;
}

.skills_content_progress_fil_2 {

    width: 50%;
    height: 12px;
    border-radius: 6px;
    background-image: linear-gradient(to right, #ff8c00, #ff0080);

}

.skills_content_progress_fil_3 {

    width: 50%;
    height: 12px;
    border-radius: 6px;
    background-image: linear-gradient(to right, #ed6ea0 0%, #ec8c69 100%);

}

.skills_content_progress_text {
    display: flex;
    flex-direction: row;
    width: inherit;
    justify-content: space-between;
    margin-bottom: 10px;
    font-family: 'Mulish', sans-serif;
    font-size: 1.12rem;
}